*{
  font-family: Arial, Helvetica, sans-serif
}

body{
  padding:  60px;
  
}

input{
  color: green;
  font-size: 1.3;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius:  6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  text-align: center;
}

.card img{
  max-width: 170px;
}